import { useEffect, useState } from 'react'
import {useRef} from 'react'
import Entrance from './Entrance'
import Gallery from './Gallery'
import Calendar from './Calendar'
import Map from './Map'
import './App.scss'

function App() {
  const expired = '2023-12-16'
  const images = [
    {
      largeURL: require('./images/pic_1.png'),
      thumbnailURL: require('./images/thumb_1.png'),
      width: 883,
      height: 1200,
    },
    {
      largeURL: require('./images/pic_2.png'),
      thumbnailURL: require('./images/thumb_2.png'),
      width: 800,
      height: 1200,
    },
    {
      largeURL: require('./images/pic_3.png'),
      thumbnailURL: require('./images/thumb_3.png'),
      width: 1200,
      height: 800,
    },
    {
      largeURL: require('./images/pic_4.png'),
      thumbnailURL: require('./images/thumb_4.png'),
      width: 876,
      height: 1200,
    },
    {
      largeURL: require('./images/pic_5.png'),
      thumbnailURL: require('./images/thumb_5.png'),
      width: 800,
      height: 1200,
    },
    {
      largeURL: require('./images/pic_6.png'),
      thumbnailURL: require('./images/thumb_6.png'),
      width: 800,
      height: 1200,
    },
    {
      largeURL: require('./images/pic_7.png'),
      thumbnailURL: require('./images/thumb_7.png'),
      width: 1200,
      height: 832,
    },
    {
      largeURL: require('./images/pic_8.png'),
      thumbnailURL: require('./images/thumb_8.png'),
      width: 800,
      height: 1200,
    },
    {
      largeURL: require('./images/pic_9.png'),
      thumbnailURL: require('./images/thumb_9.png'),
      width: 800,
      height: 1200,
    }
  ]
  const mapRef = useRef(null)
  const goMap = () => {
    mapRef.current?.scrollIntoView({behavior: 'smooth'})
  }

  const copyAccount = (account) => {
    navigator.clipboard.writeText(account)
    alert('.')
  }

  return (
    <div className="App">
      <section className="section entrance">
        <Entrance />
      </section>
      <section className="section parent serif">
        <div className="row"><span className="name"></span>  <span className="name"></span></div>
        <div><span className="name"></span>  <span className="name"></span></div>
      </section>
      <section className="section greeting serif">
        <h1 className="title"></h1>
        <div className="poem">
          <p>    ,<br />
                <br />
            <br />
            .<br /><br />
           <br />
            <br />
          .<br />
          </p>
        </div>
      </section>
      <div className="contentWrap">
        <div className="weddingInfo" onClick={goMap}>2023 12 16  11<br/>  17 </div>
        <section className="section gallery">
          <h1 className="title serif"></h1>
          <Gallery
            galleryID="wedding-gallery"
            images={images}
          />
        </section>
        <section className="section dateInfo">
          <h1 className="title serif"></h1>
          <Calendar expired={expired} />
        </section>
        <section ref={mapRef} className="section mapInfo">
          <Map />
        </section>
        <section className="section account">
          <div className="box">
            <p className="label"> </p>
            <p>(: )  3560571860473</p>
            <button type="button" className="btnCopy" onClick={() => copyAccount(' 3560571860473')}></button>
          </div>
          <div className="box">
            <p className="label"> </p>
            <p>(: )  15812148495</p>
            <button type="button" className="btnCopy" onClick={() => copyAccount(' 15812148495')}></button>
          </div>
          <p className="info">     .</p>
          <p className="info">    .<br/>  .</p>
        </section>
      </div>
    </div>
  );
}

export default App;
